import React, { PureComponent } from 'react'
import { Form, Button, Row, Col, Tooltip } from 'antd'
import style from './style/Form.scss'
import { connect } from 'react-redux'
import { mapDispatchToProps } from '../redux/mapDispatchToProps'
import { CsxSelect } from 'Form'
import {
  API_GET_LOCATION_CODEANDNAME,
  API_AFFIARS_TYPE
} from 'api'
@connect(
  state => ({ ...state.receiveOrder }),
  mapDispatchToProps
)
class App extends PureComponent {
  constructor() {
    super(...arguments)
  }
  componentWillUnmount() {
    localStorage.removeItem('b2bMmsLocation')
  }
  // 改变当前的输入值
  onInputChange(values) {
    const { searchParams, update } = this.props
    update({ searchParams: { ...searchParams, ...values } })
  }
  // 查询数据
  query = () => {
    const { handleGetList, searchParams, size } = this.props
    let reqObj = { ...searchParams }
    for (let i in reqObj) {
      if (Object.prototype.toString.call(reqObj[i]) === '[object Object]') {
        if (i != 'locationSearchKey') {
          reqObj[i] = reqObj[i].value
        }
      }
    }
    delete reqObj.initTime
    handleGetList({ searchParams: { ...reqObj, page: 1, size } })
  }

  // 重置
  reset = () => {
    const { update } = this.props
    update({ searchParams: {}, productCodeOptions: {} })
  }
  render() {
    const { searchParams = {}, menuPermissionIds } = this.props
    const {
      locationSearchKey,
      status,
      businessType
    } = searchParams
    const statusOptions = [
      { key: '0', value: '初始化' },
      { key: '1', value: '分配中' },
      { key: '2', value: '已分配' },
      { key: '3', value: '已发货' }
    ]
    console.log('locationSearchKey is', locationSearchKey)
    return (
      <Form className={style.form}>
        <Row type='flex' justify='start'>

          <Col span={8}>
            <CsxSelect
              allowClear={true}
              dateApi={API_GET_LOCATION_CODEANDNAME}
              label='地点编码/名称'
              allowClear={true}
              selectItem={{
                data: locationSearchKey,
                key: 'code',
                valueKey: ['code', 'name']
              }}
              onChangeSelect={value => {
                this.onInputChange({ locationSearchKey: value })
                console.log('value is', value)
                localStorage.setItem('b2bMmsLocation', JSON.stringify(value || {}))
              }}
            />
          </Col>
          <Col span={8}>
            <CsxSelect
              dateApi={API_AFFIARS_TYPE}
              label='业务类型'
              allowClear={true}
              selectItem={{
                data: businessType,
                key: 'key',
                valueKey: ['value']
              }}
              onChangeSelect={value => {
                this.onInputChange({ businessType: value })
              }}
            />
          </Col>
          <Col span={8}>
            <CsxSelect
              // dateApi={API_RECEIVEORDERS_WAREHOUSETYPES}
              data={statusOptions}
              label='状态'
              allowClear={true}
              selectItem={{ data: status, key: 'key', valueKey: 'value' }}
              onChangeSelect={value => {
                this.onInputChange({ status: value })
              }}
            />
          </Col>
          <Col span={8} push={16}>
            <Row type='flex' justify='end'>
            {
            menuPermissionIds && menuPermissionIds.includes('receiveOrder_find') ?
              locationSearchKey && locationSearchKey.value ?
              <Button
                type='primary'
                className={style.mr10}
                onClick={this.query}
                disabled={ locationSearchKey && locationSearchKey.value ? false : true}
                >
                查询
              </Button> :
              <Tooltip
              title='请先选择地点编码'>
                <Button
                  type='primary'
                  className={style.mr10}
                  onClick={this.query}
                  disabled={ locationSearchKey && locationSearchKey.value ? false : true}
                >
                  查询
                </Button>
              </Tooltip>
          : null

          }
            <Button type='primary' onClick={this.reset}>
              重置
            </Button>
            </Row>
          </Col>
        </Row>
      </Form>
    )
  }
}

export default Form.create()(App)



