import React, { PureComponent } from 'react'
import { Table, message } from 'antd'
import { connect } from 'react-redux'
import { mapDispatchToProps } from '../redux/mapDispatchToProps'
import { countTableScroll } from 'util'
import style from './style/Table.scss'
@connect(
  state => ({
    ...state.receiveOrder,
    userLoation: state.userLoation
  }),
  mapDispatchToProps
)
export default class app extends PureComponent {
  constructor(props) {
    super(props)
    console.log('props is', props)
    console.log('this.props is', this.props)
    this.state = {}
    this.columns = [
      {
        title: '地点编码',
        width: 180,
        dataIndex: 'location',
        key: 'location'
      },
      {
        title: '波次号',
        width: 150,
        dataIndex: 'orderCode',
        key: 'orderCode'
      },
      {
        title: '业务类型',
        width: 120,
        dataIndex: 'businessTypeDesc',
        key: 'businessTypeDesc'
      },
      {
        title: '订单数量',
        width: 70,
        dataIndex: 'orderNumber',
        key: 'orderNumber'
      },
      {
        title: '状态',
        width: 80,
        dataIndex: 'statusDesc',
        key: 'statusDesc'
      },
      {
        title: '全量分配',
        width: 60,
        dataIndex: 'allDistributeFlagDesc',
        key: 'allDistributeFlagDesc'
        // render: (text, record) => <span className={record.allDistributeFlag == 1 ? null : 'red'}>{text}</span>
      },
      {
        title: '创建人',
        width: 100,
        dataIndex: 'createBy',
        key: 'createBy'
      }, {
        title: '创建时间',
        width: 150,
        dataIndex: 'createTime',
        key: 'createTime'
      },
      {
        title: '最新分配时间',
        width: 150,
        dataIndex: 'distributeTime',
        key: 'distributeTime'
      },
      {
        title: '操作',
        width: 100,
        dataIndex: 'xxx',
        key: 'xxx',
        render: (text, record, index) => (
            <div>
               {this.renderCodeBtn(text, record, index)}

            </div>
          )
      }
    ]
  }
  renderCodeBtn(text, record, index) {
    const { menuPermissionIds } = this.props
    let dom =
              <div >
                {menuPermissionIds &&
          menuPermissionIds.includes('receiveOrder_exportTable') && (
            <span
            onClick={() => {
              const { handleExportList } = this.props
              handleExportList({ searchParams: { waveId: record.id } }, () => {
                message.success('导出成功')
              })
            }} className={style.btn_ib_table}>导出</span>
          )}
            </div>
    return dom
  }
  componentDidMount() {
  }
  componentWillReceiveProps(newProps, newStates) {
    const { handleGetList, userLoation } = this.props
    const newUserLoationValue = newProps && newProps.userLoation && newProps.userLoation.value || ''
    const userLoationValue = userLoation && userLoation.value || ''
    if (newUserLoationValue != userLoationValue) {
      handleGetList({ searchParams: { page: 1, size: newProps.size, ...newProps.searchParams } })
    }
  }
  // 排序 columns排序列
  // isAsc 是否升序 true, false
  onChange = (pagination, filters, sorter, extra) => {
    const { handleGetList, searchParams, page, size } = this.props
    if (sorter.order) {
      if (sorter.order === 'ascend') {
        handleGetList({
          searchParams: {
            page,
            size,
            ...searchParams,
            isAsc: true,
            columns: sorter.field
          }
        })
      } else {
        handleGetList({
          searchParams: {
            page,
            size,
            ...searchParams,
            isAsc: false,
            columns: sorter.field
          }
        })
      }
    } else {
      // 判断是点跳页面还是排序
      let reqObj = { ...searchParams }
      for (let i in reqObj) {
        if (Object.prototype.toString.call(reqObj[i]) === '[object Object]') {
          reqObj[i] = reqObj[i].value
        }
      }
      delete reqObj.initTime
      handleGetList({
        searchParams: {
          page: pagination.current,
          size: pagination.pageSize,
          ...reqObj
        }
      })
    }
  }
  render() {
    const {
      total,
      page,
      size,
      tableLoading,
      searchResult = []
    } = this.props
    let pagination = {
      total,
      showTotal: (total, range) => `共 ${total} 条`, // 显示总数
      showQuickJumper: true, // 是否可以快速跳转至某页
      current: page,
      pageSize: size,
      showSizeChanger: true, // 是否可以改变 pageSize
      className: 'pagination',
      pageSizeOptions: ['10', '50', '100', '200', '500']
    }
    return (
      <div className='table-group'>
        <Table
          rowKey={record => record.orderCode}
          loading={tableLoading}
          columns={this.columns}
          // dataSource={[{ orderCode: 1 }, { orderCode: 2 }]}
          dataSource={searchResult}
          scroll={{ x: countTableScroll(this.columns) }}
          onChange={this.onChange}
          pagination={pagination}
          // rowSelection={rowSelection}
        />
      </div>
    )
  }
}



