import * as actionType from './actionType'
import { getData } from 'util'
import {
  API_RECEIVEORDERS_PAGE,
  API_GETSHIPPEDORDERLIST_EXPORTEXCEL
} from 'api'
//同步
export const update = object => {
  console.log('object is ', object)
  return {
    type: actionType.RECEIVEORDER_UPDATE,
    data: object
  }
}

//异步
// 获取列表
export const handleGetList = reqData => dispatch => {
  dispatch({
    type: actionType.RECEIVEORDER_UPDATE,
    data: { tableLoading: true }
  })
  const { searchParams = {} } = reqData
  let location = '',
    locationCode = ''
  for (let i in searchParams) {
    if (Object.prototype.toString.call(searchParams[i]) === '[object Object]') {
      if (i == 'locationSearchKey') {
        location = searchParams && searchParams.locationSearchKey ? searchParams.locationSearchKey.children : ''
        locationCode = searchParams && searchParams.locationSearchKey ? searchParams.locationSearchKey.value : ''
      } else {
        searchParams[i] = searchParams[i].value
      }
    }
  }
  getData({ api: API_RECEIVEORDERS_PAGE, ...reqData })
    .then(data => {
      // 相应的逻辑判断
      let { page, size } = reqData.searchParams
      const { result = {} } = data
      const { list = [] } = result
      list && list.length > 0 && list.map((item) => {
        if (locationCode) {
          item.locationCode = locationCode
          item.location = location
        }
      })

      dispatch({
        type: actionType.RECEIVEORDER_UPDATE,
        data: {
          page,
          size,
          tableLoading: false,
          total: data.result.total,
          searchResult: data.result.list || []
        }
      })
    })
    .catch(err => {
      dispatch({
        type: actionType.RECEIVEORDER_FAIL
      })
    })
}
// 导出
export const handleExportList = (reqData, callBack) => (dispatch) => {
  getData({ api: API_GETSHIPPEDORDERLIST_EXPORTEXCEL, ...reqData }).then(data => {
    callBack && callBack()
  }).catch(err => {
    dispatch({
      type: actionType.OUTWAREHOUSELISTWAVE_FAIL
    })
  })
}



